@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.App {
  text-align: center;
  font-family: 'Oswald', sans-serif;
}

.devices-section {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  background-image: url("https://firebasestorage.googleapis.com/v0/b/tech-2f51a.appspot.com/o/accesorios-para-automatizar-sistema-seguridad-ajax.jpg?alt=media&token=a3b82712-7614-471f-90b6-79e56645179d");
  background-repeat: no-repeat;
  background-size: cover;
}

.devices-section::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: #000;
  opacity: 0.8;
}

.contenedor {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;
}

.devices-text {
  position: absolute;
  z-index: 4;
  color: rgb(255, 255, 255);
}

.mini-contenedor {
  display: flex;
  align-items: baseline;
  position: absolute;
  bottom: 0;
}

.imgs:nth-child(1) {
  position: relative;
  bottom: 0;
  width: 200px;
  z-index: 3;
}

.imgs:nth-child(2) {
  position: absolute;
  right: 10vw;
  bottom: 0;
  width: 300px;
  z-index: 2;
}

.imgs:nth-child(3) {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 250px;
  z-index: 1;
}

.device-image {
    width: 100%;
}

.control-section {
    background-color: #003756;
}

@media only screen and (max-width: 600px) {
  .imgs:nth-child(2) {
    display: none;
  }

  .devices-section {
    background-position: 50%;
    background-size: 170vh;
  }

  .imgs:nth-child(3) {
    width: 200px;
  }

  @media (prefers-reduced-motion: no-preference) {
    .imgs:nth-child(1) {
      -webkit-animation: imagen1 infinite 3s linear alternate;
              animation: imagen1 infinite 3s linear alternate;
    }
  }

  @-webkit-keyframes imagen1 {
    from {
      left: 0%;
    }

    to {
      left: 40%;
    }
  }

  @keyframes imagen1 {
    from {
      left: 0%;
    }

    to {
      left: 40%;
    }
  }
}

@media only screen and (min-width: 900px) {
  @media (prefers-reduced-motion: no-preference) {
    .imgs:nth-child(1) {
      -webkit-animation: imagen1 10s infinite linear alternate;
              animation: imagen1 10s infinite linear alternate;
    }
  }

  @-webkit-keyframes imagen1 {
    from {
      left: 0%;
    }
    to {
      left: 80%;
    }
  }

  @keyframes imagen1 {
    from {
      left: 0%;
    }
    to {
      left: 80%;
    }
  }
}



.tech-section {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: url("https://firebasestorage.googleapis.com/v0/b/tech-2f51a.appspot.com/o/tecnologias.jpg?alt=media&token=1948aa04-5efd-4e73-8ba3-96bd5709c01c");
  background-repeat: no-repeat;
  background-size: 100%;
}

.tech-section::before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #000;
  opacity: 0.7;
}

@media only screen and (max-width: 600px){
    .tech-section {
        background-size: 150vh;
        background-position: 54%;
    }
}

.home-section {
  background-image: url(https://firebasestorage.googleapis.com/v0/b/tech-2f51a.appspot.com/o/smart-home.jpg?alt=media&token=77313205-52dc-4be2-94a2-44a72dbde565);
  background-repeat: no-repeat;
  background-size: 100%;
  min-height: 100vh;
  display: flex;
  align-items: center;
  position: relative;
}
.home-section::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: #000;
    opacity: 0.7;
}
.main-letter {
    font-size: 100px;
}
.unete-section {
    background-color: #e9e9e9;
}
.imagen {
    width: 700px;
}
.list-group-item {
    background-color: #e9e9e9;
}

@media only screen and (max-width: 600px) {
  .home-section {
      min-height: 90vh;
      background-size: 200vh;
      top: 50px;
  }
  .main-letter {
      font-size: 70px;
  }
  .imagen {
      width: 100%;
  }
}

.nav-link { 
    text-decoration: none;
    color: rgb(255, 255, 255);
    transition: .5s;
}

.nav-link:hover {
    color: rgb(161, 161, 161);
}
